.leadership-card {
  display: block;
  margin-bottom: 64px;
  position: relative;

  img {
    border-radius: 4px;
    display: block;
    position: relative;
    width: 100%;
  }

  .cta {
    background: linear-gradient(to top, rgba(0, 0, 0, 0.9) 20%, rgba(0, 0, 0, 0.3) 80%);
    color: #fff;
    height: 100%;
    opacity: 0;
    position: absolute;
    text-align: center;
    top: 0;
    transition: .4s;
    visibility: hidden;
    width: 100%;

    p {
      left: 0;
      margin-top: -50px;
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-35px);
      transition: .4s;
    }
  }

  &:hover {
    dl {
      background: transparent;
      bottom: 34%;
      box-shadow: none;

      dt {
        color: #fff;
      }

      dd {
        color: #eee;
      }
    }

    .cta {
      opacity: 1;
      visibility: visible;

      p {
        transform: translateY(0);
      }
    }
  }

  dl {
    background: #fff;
    border-color: #e6e6e6;
    border-radius: 3px;
    bottom: -32px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
    left: 24px;
    padding: 12px 0;
    position: absolute;
    right: 24px;
    text-align: center;
    transition: .4s;
    z-index: 1;



    dt {
      color: #212529;
      font-family: $heading-font-family;
      font-size: 18px;
      font-weight: normal;
      text-transform: uppercase;
    }

    dd {
      color: #666666;
      font-family: $base-font-family;
    }
  }
}
